import {Component, OnInit, Output, EventEmitter} from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {ServerDataService} from '../../../services/server-data.service';
import {SettingService} from '../../../services/setting.service';
import {Http} from '@angular/http';
import {MatSnackBar} from '@angular/material';

import 'rxjs/add/operator/startWith';
import 'rxjs/add/operator/map';

@Component({
  selector: 'app-billing-details',
  templateUrl: './billing-details.component.html',
  styleUrls: ['./billing-details.component.css']
})
export class BillingDetailsComponent implements OnInit {
  street: string;
  state: string;
  city: string;
  zip: string;
  firstName: string;
  lastName: string;
  email: string;
  phoneNumber: string;

  loader = false;
  formLoader = false;

  thirdFormGroup: FormGroup;

  @Output() billingDataReady = new EventEmitter<{ billingInfo: object }>();

  constructor(private _formBuilder: FormBuilder,
              private http: Http,
              private globalSettings: SettingService,
              private _serverDataService: ServerDataService,
              public snackBar: MatSnackBar) {
  }

  ngOnInit() {
    this.thirdFormGroup = this._formBuilder.group({
      // streetCtrl: [''],
      // stateCtrl: [''],
      // cityCtrl : [''],
      // zipCtrl : [''],
      // firstNameCtrl : [''],
      // lastNameCtrl : [''],
      // emailCtrl : [''],
      // phoneNumberCtrl : ['']

      streetCtrl: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      stateCtrl: ['', Validators.compose([Validators.required, Validators.minLength(2),
        Validators.maxLength(2), Validators.pattern('^[A-Z]+$')])],
      cityCtrl: ['', Validators.compose([Validators.required, Validators.minLength(2), Validators.pattern('^[a-zA-Z ,\-]+$')])],
      zipCtrl: ['', Validators.compose([Validators.required, Validators.minLength(5),
        Validators.pattern('^(\\d{5})$|^(\\d{5})+(-(\\d{4}))$')])],
      firstNameCtrl: ['', Validators.compose([Validators.required, Validators.minLength(2), Validators.pattern('^[a-zA-Z]+$')])],
      lastNameCtrl: ['', Validators.compose([Validators.required, Validators.minLength(2), Validators.pattern('^[a-zA-Z]+$')])],
      emailCtrl: ['', Validators.compose([Validators.required, Validators.email])],
      phoneNumberCtrl: ['', Validators.compose([Validators.required, Validators.minLength(10),
        Validators.maxLength(11), Validators.pattern('^(1)+(\\d{10})$|^(1)+(\\d{11})$')])]
    });
  }

  isDisabled() {
    if (this.thirdFormGroup.get('streetCtrl').valid &&
      this.thirdFormGroup.get('stateCtrl').valid &&
      this.thirdFormGroup.get('cityCtrl').valid &&
      this.thirdFormGroup.get('zipCtrl').valid &&
      this.thirdFormGroup.get('firstNameCtrl').valid &&
      this.thirdFormGroup.get('lastNameCtrl').valid &&
      this.thirdFormGroup.get('emailCtrl').valid &&
      this.thirdFormGroup.get('phoneNumberCtrl').valid) {
      return false;
    }
    return true;
  }

  saveDetails() {
    this.formLoader = true;
    // this.loader = true;
    // this.thirdFormGroup.disable();

    const billingInfo: any = {};
    billingInfo.address = this.street;
    billingInfo.state = this.state;
    billingInfo.city = this.city;
    billingInfo.zip = this.zip;
    billingInfo.contactFirstName = this.firstName;
    billingInfo.contactLastName = this.lastName;
    billingInfo.contactEmail = this.email;
    billingInfo.contactPhone = this.phoneNumber;

    this.globalSettings.setBillingInfo(billingInfo).subscribe(
      result => {
        this.billingDataReady.emit({billingInfo});
        // this.loader = false;
        this.formLoader = false;
      },
      error => {
        // this.loader = false;
        this.formLoader = false;
        this.snackBar.open('Some error occurring. Pls cehck data and try again. ', '', {
          duration: 3000
        });

      }
    );

    // this._serverDataService.saveBillingDetails2(billingInfo)
    //   .subscribe(
    //   result => {}
    // );
  }

}



