import { Component, Input, OnInit } from '@angular/core';
import {WidgetDialogComponent} from './widget-dialog/widget-dialog.component';
import {MatDialog} from '@angular/material';

@Component({
  selector: 'app-service-item',
  templateUrl: './service-item.component.html',
  styleUrls: ['./service-item.component.css']
})
export class ServiceItemComponent implements OnInit {
  @Input() widgetTitle: string;
  @Input() widgetDescription: number;
  @Input() widgetIcon: string;
  @Input() widgetURL: string;

  constructor(public dialog: MatDialog) { }

  ngOnInit() {
  }

  openWidget(): void {
    const dialogRef = this.dialog.open(WidgetDialogComponent, {
      height: '90vh',
      width: '800px',
      data: {'url' : this.widgetURL }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }

}



