import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {UserService} from '../../../services/user.service';
import {Observable} from 'rxjs/Observable';
import {SettingService} from '../../../services/setting.service';

@Component({
  selector: 'app-roles-and-users',
  templateUrl: './roles-and-users.component.html',
  styleUrls: ['./roles-and-users.component.css']
})
export class RolesAndUsersComponent implements OnInit {
  roles = [];
  selectedRoles = [];
  loader = false;
  formLoader = false;
  @Output() isRolesApplied = new EventEmitter<{ isRolesApplied: boolean }>();

  secondFormGroup: FormGroup;
  myControl: FormControl = new FormControl();

  filteredOptions: Observable<string[]>;


  constructor(private userSys: UserService,
              private globalSettings: SettingService,
              private _formBuilder: FormBuilder) {
  }

  ngOnInit() {
    this.userSys.getRoles().subscribe(response => this.roles = response.result);
    this.secondFormGroup = this._formBuilder.group({});

    this.filteredOptions = this.myControl.valueChanges
      .startWith(null)
      .map(val => val ? this.filter(val) : this.roles.slice());
  }


  filter(val: string): string[] {
    return this.roles.filter(role =>
      role.name.toLowerCase().indexOf(val.toLowerCase()) === 0);
  }

  onRoleSelect(role) {
    if (this.selectedRoles.indexOf(role) === -1) {
      this.selectedRoles.push(role);
    }
  }

  onSubmit() {
    this.formLoader = true;
    // this.loader = true;
    this.globalSettings.saveRolesProgress().subscribe(
      response => {
        // this.loader = false;
        this.formLoader = false;
        this.isRolesApplied.emit({isRolesApplied: true});
      },
      error => {
        this.formLoader = false;
      }
    )
  }

  removeRole(role) {
    const i = this.selectedRoles.indexOf(role);
    if (i >= 0) {
      this.selectedRoles.splice(i, 1);
    }
  }

}



