import {Component, OnInit, Output, EventEmitter} from '@angular/core';
import {ActivationModel} from '../../shared/activation.model';
import {SettingService} from '../../services/setting.service';
import {Response} from '@angular/http';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';

@Component({
  selector: 'app-body-activation',
  templateUrl: './body-activation.component.html',
  styleUrls: ['./body-activation.component.css']
})
export class BodyActivationComponent implements OnInit {
  @Output() isSubmited = new EventEmitter<{ isSubscribed: boolean }>();
  isSubscribed = true;
  activationData = new ActivationModel();
  currentSysId: string;
  formLoader = false;

  activationFormGroup: FormGroup;

  constructor(private _formBuilder: FormBuilder,
              private settingService: SettingService) {
  }

  ngOnInit() {
    this.activationFormGroup = this._formBuilder.group({
      companyNameCtrl: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      accountNumberCtrl: ['', Validators.compose([Validators.required, Validators.minLength(8),
        Validators.maxLength(8), Validators.pattern('^[0-9]+$')])],
      fullNameCtrl: ['', Validators.compose([Validators.required, Validators.minLength(3), Validators.pattern('^[a-zA-Z ,.\'-]+$')])],
      emailCtrl: ['', Validators.compose([Validators.required, Validators.email])],
    });
    this.settingService.checkActivation()
      .subscribe(
        response => {
          // let data = response.json();
          const data = response;
          if (data.result && data.result[0] && data.result[0].is_activated) {
            this.scrollToTop();
            console.log('-- isSubscribed defined as:', data.result[0].is_activated);
            this.isSubmited.emit({isSubscribed: true});
            this.settingService.changeSysId(data.result[0].sys_id);
          }
        },
        (err) => {
          console.log('some error when activation check:', err);
        },
      );
  }

  onSubmit() {
    this.formLoader = true;
    // this.settingService.currentSysId.subscribe(currentSysId => this.currentSysId = currentSysId)

    this.settingService.setSettings(this.activationData)
      .subscribe(
        (response: Response) => {
          const data = response.json();
          // console.log('all settings:', data.result.sys_id);
          this.settingService.changeSysId(data.result.sys_id);
          this.isSubmited.emit({isSubscribed: this.isSubscribed});
          this.formLoader = false;
        },
        (err) => {
          console.log('some error:', err);
          this.formLoader = false;
        },
      );

    this.scrollToTop();
    // this.isSubscribed = true;
    // setTimeout(() => {
    //   this.isSubscribed = true;
    // }, 2000);
  }

  isDisabled() {
    if (this.activationFormGroup.get('companyNameCtrl').valid &&
      this.activationFormGroup.get('accountNumberCtrl').valid &&
      this.activationFormGroup.get('fullNameCtrl').valid &&
      this.activationFormGroup.get('emailCtrl').valid) {
      return false
    }
    return true;
  }

  scrollToTop() {
    window.scroll({
      top: 0,
      behavior: 'smooth'
    });
  }

}



