import {Component, OnInit, Output, EventEmitter} from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {SettingService} from '../../../services/setting.service';
import {MatSnackBar} from '@angular/material';

import 'rxjs/add/operator/startWith';
import 'rxjs/add/operator/map';

@Component({
  selector: 'app-api-activation',
  templateUrl: './api-activation.component.html',
  styleUrls: ['./api-activation.component.css']
})
export class ApiActivationComponent implements OnInit {
  firstFormGroup: FormGroup;
  currentSysId: string;
  loader1 = false;
  token: string;
  password: string;
  passwordConfirm: string;
  formLoader = false;

  goodToken = false;

  @Output() isTokenApplied = new EventEmitter<{ tokenApplied: boolean }>();
  tokenApplied = false;

  constructor(private _formBuilder: FormBuilder,
              private globalSettings: SettingService,
              public snackBar: MatSnackBar) {
  }

  ngOnInit() {
    this.globalSettings.currentSysId.subscribe(currentSysId => this.currentSysId = currentSysId);
    this.firstFormGroup = this._formBuilder.group({
      tokenCtrl: ['', Validators.required],
      passwordCtrl: ['', Validators.compose([Validators.required, Validators.minLength(8)])],
      passwordConfirmCtrl: ['', Validators.required]
    });

  }


  checkToken() {
    // this.firstFormGroup.disable();

    // if (this.token && this.token.length > 5) {
    //   this.loader1 = true;
    //   // this.firstFormGroup.disable();
    //   // this.stepper.selected.editable = false;   -----------------<<<<<<<<<<<<<<<<< !!!!
    //
    //   try {
    //     const decodedToken = JSON.parse(atob(this.token));
    //     if (decodedToken.tenantCode && decodedToken.tenantUser && decodedToken.tenantPassword && decodedToken.carrierId) {
    //       // this.globalSettings.setToken(this.token, decodedToken, this.currentSysId)
    //       this.globalSettings.setToken(this.token, decodedToken)
    //         .subscribe(
    //           result => {
    //             console.log('success');
    //             this.goodToken = true;
    //             setTimeout(() => {
    //               this.isTokenApplied.emit({tokenApplied: true});
    //               // this.tokenApplied = true;
    //               // this.stepper.selectedIndex = 1;  -----------------<<<<<<<<<<<<<<<<< !!!!
    //             }, 500);
    //           },
    //           error => {
    //             console.log('error', error);
    //           },
    //           () => {
    //             console.log('complited')
    //             this.loader1 = false;
    //           }
    //         );
    //     }
    //   } catch (e) {
    //     // console.log('error', e.toString());
    //     this.loader1 = false;
    //     return false;
    //   }
    // }
  }

  setTokenAndPassw() {
    this.formLoader = true;
    // this.firstFormGroup.get('tokenCtrl').hasError('ssssssssssssss')
    // this.loader1 = true;

    // this.firstFormGroup.controls['tokenCtrl'].valueChanges.subscribe(console.log);

    const isValidToken = this.verifyToken();
    const isValidPassw = this.verifyPassword();

    // this.firstFormGroup.controls['passwordCtrl'].setErrors({'incorrect': true});
    // console.log('isValidToken',isValidToken);
    // console.log('isValidPassw',isValidPassw);
    const data: any = {};
    data.token = this.token;
    data.servicePassword = this.password;

    if (isValidToken && isValidPassw) {
      this.globalSettings.saveTokenAndPassword(data)
        .subscribe(
          result => {
            console.log('success');
            this.goodToken = true;
            setTimeout(() => {
              this.isTokenApplied.emit({tokenApplied: true});
              // this.tokenApplied = true;
              // this.stepper.selectedIndex = 1;  -----------------<<<<<<<<<<<<<<<<< !!!!
            }, 500);
            // this.globalSettings.pullCatalog().subscribe();
            // this.loader1;
            this.formLoader = false;
          },
          error => {
            console.log('error', error);
            // this.snackBar.open('Some error occurring. Pls check data and try again. ', '', {
            this.snackBar.open(error.statusText, '', {
              duration: 3000
            });
            this.formLoader = false;
            // setTimeout(() => {
            //   // this.tokenApplied = true;
            //   // this.stepper.selectedIndex = 1;  -----------------<<<<<<<<<<<<<<<<< !!!!
            // }, 500);
          },
          () => {
            console.log('complited')
            // this.loader1 = false;
            this.formLoader = false;
          }
        );
    } else {
      // this.loader1 = false;
      this.formLoader = false;
    }
    return;
  }

  verifyToken() {
    if (this.token && this.token.length > 0) {
      try {
        const decodedToken = JSON.parse(atob(this.token));
        if (decodedToken.tenantCode && decodedToken.tenantUser && decodedToken.tenantPassword && decodedToken.carrierId) {
          return true;
        } else {
          console.log('invalid Token');
          return false;
        }
      } catch (e) {
        console.log('error', e.toString());
        return false;
      }
    }
    return false;
  }

  verifyPassword() {
    if (this.password.length > 0 && (this.password !== this.passwordConfirm)) {
      return false
    }
    return true;
  }

  isDisabled() {
    if (this.firstFormGroup.get('tokenCtrl').valid &&
      this.firstFormGroup.get('passwordCtrl').valid &&
      this.firstFormGroup.get('passwordConfirmCtrl').valid) {
      return false
    }
    return true;
  }


}



