import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup} from '@angular/forms';
import {SettingService} from '../../../services/setting.service';

@Component({
  selector: 'app-catalog-import',
  templateUrl: './catalog-import.component.html',
  styleUrls: ['./catalog-import.component.css']
})
export class CatalogImportComponent implements OnInit {
  fifthFormGroup: FormGroup;
  isCatalogReady: boolean;
  catalogSummary: any;
  currentSysId: string;

  constructor(private _formBuilder: FormBuilder,
              private globalSettings: SettingService) {
  }

  ngOnInit() {
    this.globalSettings.currentSysId.subscribe(currentSysId => this.currentSysId = currentSysId);
    this.checkCatalogLoading();
    this.fifthFormGroup = this._formBuilder.group({});
  }


  checkCatalogLoading() {
    // console.log('checkCatalogLoading');
    // console.log('checkCatalogLoading this.currentSysId', this.currentSysId.length);
    if (this.currentSysId && this.currentSysId.length > 0) {
      this.globalSettings.checkCatalog()
        .subscribe(
          response => {
            const resp = response.json();
            if (resp.result && resp.result.summary) {

              // need to fix
              // if (resp.result && resp.result.status) {
              // this.isCatalogReady = resp.result.status;
              this.isCatalogReady = true;
              this.catalogSummary = resp.result.summary;
            }
            // console.log('catalog resp', resp)
          }
        );
    }
  }

}



