import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {HttpModule} from '@angular/http';
import {Routes, RouterModule} from '@angular/router';
import {FlexLayoutModule} from '@angular/flex-layout';

import {MyCustomMaterialModule} from './___temp/material2.module';


import {AppComponent} from './app.component';
import {HeaderComponent} from './setup-guide/header/header.component';
import {LogoComponent} from './setup-guide/header/logo/logo.component';
// import { DeviceDialogComponent } from './setup-guide/body-stepper/device-dialog/device-dialog.component';

import {SettingService} from './services/setting.service';
import {ServerDataService} from './services/server-data.service';
import {UserService} from './services/user.service';

import {BodyActivatedComponent} from './setup-guide/body-activated/body-activated.component';
import {BodyActivationComponent} from './setup-guide/body-activation/body-activation.component';
import {BodyStepperComponent} from './setup-guide/body-stepper/body-stepper.component';

import {RolesAndUsersComponent} from './setup-guide/body-stepper/roles-and-users/roles-and-users.component';
import {BillingDetailsComponent} from './setup-guide/body-stepper/billing-details/billing-details.component';
import {BillingAccountsComponent} from './setup-guide/body-stepper/billing-accounts/billing-accounts.component';
import {CatalogImportComponent} from './setup-guide/body-stepper/catalog-import/catalog-import.component';
import {ApiActivationComponent} from './setup-guide/body-stepper/api-activation/api-activation.component';

import {SetupGuideComponent} from './setup-guide/setup-guide.component';
import {NotFoundComponent} from './not-found/not-found.component';
import {ServicePanelComponent} from './service-panel/service-panel.component';
import { ServiceItemComponent } from './service-panel/service-item/service-item.component';
import { WidgetDialogComponent } from './service-panel/service-item/widget-dialog/widget-dialog.component';

const appRoutes: Routes = [
  { path: '', component: SetupGuideComponent},
  { path: '**', component: ServicePanelComponent}, // temp!!
  { path: 'service-panel', component: ServicePanelComponent},
  { path: '**', component: NotFoundComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    LogoComponent,
    BodyActivationComponent,
    BodyStepperComponent,
    // DeviceDialogComponent,
    RolesAndUsersComponent,
    BillingDetailsComponent,
    BillingAccountsComponent,
    CatalogImportComponent,
    ApiActivationComponent,
    BodyActivatedComponent,
    SetupGuideComponent,
    NotFoundComponent,
    ServicePanelComponent,
    ServiceItemComponent,
    WidgetDialogComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes),
    FlexLayoutModule,
    ReactiveFormsModule,
    MyCustomMaterialModule,
    BrowserAnimationsModule
  ],
  entryComponents: [
    WidgetDialogComponent
    // DeviceDialogComponent,
  ],
  providers: [
    // DeviceDialogComponent,
    WidgetDialogComponent,
    SettingService,
    ServerDataService,
    UserService
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
}



