import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {MatSnackBar} from '@angular/material';

import {SettingService} from '../../../services/setting.service';

@Component({
  selector: 'app-billing-accounts',
  templateUrl: './billing-accounts.component.html',
  styleUrls: ['./billing-accounts.component.css']
})
export class BillingAccountsComponent implements OnInit {
  ban: string;
  ctn: string;

  loader = false;
  loaderAcc = false;
  loaderValidate = false;
  formLoader = false;

  billingAccounts: Array<any>;
  visibleNewForm = true;

  fourthFormGroup: FormGroup;

  @Output() isBanApplied = new EventEmitter<{ isBanReady: boolean }>();

  constructor(private _formBuilder: FormBuilder,
              private globalSettings: SettingService,
              public snackBar: MatSnackBar) {
  }

  ngOnInit() {
    this.getAccounts();
    this.fourthFormGroup = this._formBuilder.group({
      banCtrl: ['', Validators.compose([Validators.required, Validators.pattern('^(\\d{12})$')])],
      ctnCtrl: ['', Validators.compose([Validators.required, Validators.pattern('^(\\d{10})$|^(\\d{11})$')])]
    });
  }


  isDisabled() {
    if (this.fourthFormGroup.get('banCtrl').valid &&
      this.fourthFormGroup.get('ctnCtrl').valid) {
      return false;
    }
    return true;
  }


  getAccounts() {
    this.formLoader = true;
    console.log('getAccounts')
    this.globalSettings.getBillingAccounts().subscribe(
      response => {
        const data = response.json();
        console.log('getAccounts data', data)
        if (data && data.result) {
          this.billingAccounts = data.result;
        }
        this.formLoader = false;
      },
      error => {
        this.formLoader = false;
      }
    );
    this.loader = false;
  }


  showNewForm() {
    console.log('showNewForm');
    this.visibleNewForm = true;
  }


  addBan() {
    this.loaderValidate = true;

    const data: any = {};
    data.ban = this.ban;
    data.ctn = this.ctn;

    this.globalSettings.validateBillingAccount(data).subscribe(
      result => {
        this.globalSettings.setBillingAccount(data).subscribe(
          success => {
            this.visibleNewForm = false;
            this.getAccounts();
            this.loaderValidate = false;
            this.ban = '';
            this.ctn = '';
          },
          error => {
            this.loaderValidate = false;
            this.snackBar.open('Something went wrong', '', {
              duration: 3000
            });
          }
        )
      },
      error => {
        this.loaderValidate = false;
        this.snackBar.open('Some error occurring. Pls cehck data and try again. ', '', {
          duration: 3000
        });
      }
    )
  }

  removeBan(ban) {
    this.loaderAcc = true;
    this.loader = true;
    this.globalSettings.removeBillingAccount(ban.sys_id).subscribe(
      result => {
        this.getAccounts();
        this.loaderAcc = false;
      },
      error => {
        this.loaderAcc = false;
      }
    )
  }

  onSubmit() {
    this.formLoader = true;
    // this.loader = true;
    this.globalSettings.saveBANProgress().subscribe(
      response => {
        // this.loader = false;
        this.formLoader = false;
        this.isBanApplied.emit({isBanReady: true});
      },
      error => {
        this.formLoader = false;
      }
    )
  }

}



