import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {SettingService} from '../../services/setting.service';
import {Response} from '@angular/http';

@Component({
  selector: 'app-body-activated',
  templateUrl: './body-activated.component.html',
  styleUrls: ['./body-activated.component.css']
})
export class BodyActivatedComponent implements OnInit {
  @Output() isActivated = new EventEmitter<{ isActivated: boolean }>();
  requestInfo: any = {};
  formLoader = true;

  constructor(private settingService: SettingService) {
  }

  ngOnInit() {
    this.formLoader = true;
    this.settingService.getRequestInfo()
      .subscribe(
        (response: Response) => {
          let data = response.json();
          if (data.result && data.result[0]) {
            // console.log('data.result[0]',data.result[0])
            this.requestInfo = data.result[0];
          }
          this.formLoader = false;
        },
        (err) => {
          this.formLoader = false;
          console.log('some error when activation check:', err);
        },
      );
  }

  haveToken() {
    this.formLoader = true;
    // console.log('haveToken');
    this.settingService.haveToken().subscribe(
      response => {
        this.isActivated.emit({isActivated: true});
        this.formLoader = false;
      },
      error => {
        this.formLoader = false;
      }
    );

  }

}



